.special-span {
  font-size: 1rem;
  color: #fff;
  background: none;
  border: none;
  border-radius: 50%;
  position: absolute;
  z-index: 0;
  transition: 0.3s;
}
.special-span:before {
  content: "";
  position: absolute;
  inset: -8px;
  padding: 10px;
  border-radius: 50%;
  background: conic-gradient(
    #ff53bb,
    #0000 30deg 120deg,
    #00f8d3 150deg 180deg,
    #0000 210deg 300deg,
    #ff53bb 330deg
  );
  -webkit-mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
  -webkit-mask-composite: xor;
  mask-composite: intersect;
}
.special-span:after {
  content: "";
  position: absolute;
  inset: -4rem;
  background: radial-gradient(
      7rem at left 14rem top 5rem,
      #ff53bb 100%,
      #0000
    ),
    radial-gradient(7rem at right 14rem bottom 5rem, #00f8d3 100%, #0000);
  filter: blur(4rem);
  opacity: 0.5;
}

.special-span:before,
.special-span:after {
  transition: 0.5s, 99999s 99999s transform;
}
.special-span:hover {
  box-shadow: 0 0 0 1px #666;
}

.special-span:before {
  background-color: #222;
  border: 2px solid #333;
}

.rotating-endless{
    transform: rotate(36000deg);
    transition: 0.5s, 600s linear transform;
}




@media screen and (max-width:600px) {
    .special-span{
        width: 50px;
        height: 50px;
        top:55%;
    }
}

@media screen and (min-width:600px) {
    .special-span{
        width: 100px;
        height: 100px;
        top:57%;
    }
}

@media screen and (min-width:1200px) {
    .special-span{
        width: 150px;
        height: 150px;
        top:55%;
    }
}

@media screen and (min-width:1800px) {
    .special-span{
        width: 300px;
        height: 300px;
        top:50%;
    }
}

@media screen and (min-width:2200px) {
  .special-span{
      width: 300px;
      height: 300px;
      top:55%;
  }
}



