.submit-button {
  background-color: rgb(242, 242, 246);
  color: rgb(4, 4, 4);
  border-radius: 30px;
  font-size: 1.4em;
  cursor: pointer;
}

#resume-link {
  margin-top: 1em;
  font-size: 1.5em;
  background-color: black;
  border-radius: 1em;
  width: fit-content;
  padding: 0.3em;
  cursor: pointer;
  color: red;
  text-decoration: none;
  border: 1px solid #aaa;
}
#resume-link:hover {
  background-color: #888;
}

#contact {
  display: flex;
  padding: 4vh 2vw;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.contact-socials {
  margin-bottom: 10%;
  width: 48vw;
}

.socials-container {
  font-size: 4rem;
  font-weight: bold;
}

.link-image {
  height: 2.3rem;
  width: 2.3rem;
  align-self: center;
}

.send-email {
  margin: 5% auto;
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
}

#send-email-result {
  color: #e5f4e3;
  font-size: 1.4em;
  height: 1.4em;
  text-align: center;
}

.email-input {
  background: transparent;
  border: none;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  font-size: 1.4em;
}

.submit-div {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 1em;
  margin-top: 1em;
}

@media only screen and (max-width: 1000px) {
  #contact {
    flex-direction: column;
  }

  .socials-container {
    height: 30rem;
    top: 15%;
  }
  .email-input {
    width: 250px;
  }
}

@media only screen and (max-width: 600px) {
  .contact-socials {
    margin-top: 30%;
    position: relative;
    right: 50px;
  }
}

@media only screen and (min-width: 600px) {
  .contact-socials {
    margin-top: 10%;
    position: relative;
    right: 50px;
  }
}

@media screen and (min-width: 3000px) {
  .socials-container {
    position: relative;
    left: 10vw;
  }
}
@media screen and (min-width: 1000px) {
  .socials-container {
    position: relative;
    left: 5vw;
  }
}

@media screen and (max-width: 1100px) {
  .send-email {
    margin: 0;
  }
  .email-input {
    max-width: 100%;
  }
  #contact {
    justify-content: space-between;
  }
}

@media screen and (max-width: 800px) {
  .send-email {
    margin: 0;
    width: 70%;
    align-items: center;
  }
  .email-input {
    max-width: fit-content;
  }
}

@media screen and (max-width: 800px) {
  .contact-section {
    flex-direction: column;
  }
}
